<template>
  <div class="wrap">
      <mt-swipe :auto="4000">
        <mt-swipe-item>
            <img src="../../../static/imgs/login/tdf_login_bg1.png" alt="">
        </mt-swipe-item>
        <mt-swipe-item>
            <img src="../../../static/imgs/login/tdf_login_bg2.png" alt="">
        </mt-swipe-item>
        <mt-swipe-item>
            <img src="../../../static/imgs/login/tdf_login_bg3.png" alt="">
        </mt-swipe-item>
    </mt-swipe>
    <div class="btn_box">
        <a href="javascript:;" @click="popPage"></a>
        <a href="javascript:;" @click="wxPop"></a>
    </div>
    <!--  -->
    <mt-popup v-model="popupVisible" position="bottom">
        <slot>
            <div class="layerout">
                <div class="layer_head">
                    <b></b>
                    <h3></h3>
                    <a href="javascript:;" @click="closePop"></a>
                </div>
                <div class="layer_body">
                    <ul>
                        <li>
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="wrongTel">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="!hadAgree">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="hadTel" class="user_say">
                            <img src="../../../static/imgs/login/tdf_login_user.png" alt="">
                            <span>{{userTel}}</span>
                        </li>
                        <li v-if="codeMsg">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="codeTextShow" class="user_say">
                            <img src="../../../static/imgs/login/tdf_login_user.png" alt="">
                            <span>{{userCode}}</span>
                        </li>
                        <li v-if="codeWrong">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="codeWrong">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span>
                                <br />
                                <b @click="callCode">&nbsp;&nbsp;[]</b>
                            </span>
                        </li>
                        <li v-if="callMsg">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span></span>
                        </li>
                        <li v-if="successTextShow">
                            <img src="../../../static/imgs/login/tdf_login_logo.png" alt="">
                            <span>...</span>
                        </li>
                    </ul>
                </div>
                <div class="layer_bottom">
                    <div class="bot_top">
                        <img :src="noAggrementUrl" alt="" @click="agreeOnot">
                        <span></span>
                        <router-link tag="a" :to="{name:'userAgreement'}" href="javascript:;"></router-link>
                    </div>
                    <div class="bot_input" v-if="telInputShow">
                        <span>CNN +86</span>
                            <input type="tel" v-model.trim="userTel" placeholder="">
                            <a href="javascript:;" @click="getCode"></a>
                    </div>
                    <div class="bot_input" v-if="!telInputShow">
                            <input type="tel" v-model.trim="userCode" placeholder="">
                            <a href="javascript:;" @click="codeConfirm"></a>
                    </div>
                </div>
            </div>
        </slot>
    </mt-popup>
  </div>    
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name: "loginIndex",
  data() {
    return {
      popupVisible: false,
      noAggrementUrl: require("../../../static/imgs/login/tdf_login_select_pre.png"),
      hadAgree: true,
      //
      telInputShow: true,
      wrongTel: false,
      hadTel: false,
      codeMsg: false,
      codeWrong: false,
      codeTextShow: false,
      callMsg: false,
      successTextShow: false,
      userTel: "",
      userCode: ""
    };
  },
  methods: {
    popPage() {
      this.popupVisible = true;
    },
    closePop() {
      this.popupVisible = false;
    },
    agreeOnot() {
      //
      if (this.hadAgree) {
        this.noAggrementUrl = require("../../../static/imgs/login/tdf_login_select.png");
        this.hadAgree = false;
      } else {
        this.noAggrementUrl = require("../../../static/imgs/login/tdf_login_select_pre.png");
        this.hadAgree = true;
      }
    },
    getCode() {
      //
      let userTel = this.userTel;
      if(!this.hadAgree) {
          this.hadAgree = false;
          return false;
      }
      if (!this.regTel.test(userTel)) {
        this.wrongTel = true;
        this.userTel = "";
      } else {
        this.wrongTel = false;
        this.hadTel = true;
        this.$http
          .post(this.baseUrl + "message/sendMessageCode", {
            mobilePhone: userTel
          })
          .then(res => {
            if (res.body.statusCode == 1) {
              this.codeMsg = true;
              this.telInputShow = false;
            }
          });
      }
    },
    //
    callCode() {
      this.callMsg = true;
    },
    //
    codeConfirm() {
      let userCode = this.userCode;
      if (!this.regCode.test(userCode)) {
        this.codeWrong = true;
      } else {
        this.codeWrong = false;
        this.codeTextShow = true;
        this.$http
          .post(this.baseUrl + "logInOut/login", {
            mobilePhone: this.userTel,
            messageCode: this.userCode
          })
          .then(res => {
            //localStorageuserInfo

            // localStorage.setItem('userInfo',JSON.stringify(res.body.object));
            if (res.body.statusCode == 1) {
              this.successTextShow = true;
              setTimeout(()=>{
                  this.$router.push({name:'settingNameLogo'});
              },1000);
            }else if(res.body.statusCode == 0) {
                this.codeWrong = true;
                this.codeTextShow = false;
            }
          });
      }
    },
    wxPop() {
        
    }
  }
};
</script>

<style lang="less" scoped>
.wrap {
  min-width: 320px;
  max-width: 640px;
  margin: 0 auto;
  height: 100%;
  position: relative;
  img {
    height: 100%;
    width: 100%;
    display: block;
  }
  .btn_box {
    position: absolute;
    bottom: 1.5rem;
    display: flex;
    width: 80%;
    left: 50%;
    transform: translateX(-50%);
    justify-content: space-between;
    a {
      width: 48%;
      line-height: 2rem;
      background-color: #2d8eff;
      color: #fff;
      text-align: center;
      border-radius: 0.4rem;
    }
  }
  .mint-popup {
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: transparent;
    background-color: #eff4fa;
    .layerout {
      .layer_head {
        display: flex;
        justify-content: space-between;
        padding: 0 1rem;
        background-color: #2d8eff;
        height: 2rem;
        align-items: center;
        border-top-left-radius: 0.4rem;
        border-top-right-radius: 0.4rem;
        h3 {
          color: #fff;
          font-size: 0.85rem;
        }
        a {
          color: #fff;
          font-size: 0.65rem;
        }
      }
      .layer_body {
        background-color: #eff4fa;
        padding: 0 0.6rem;
        height: 100%;
        li {
          display: flex;
          justify-content: flex-start;
          padding-top: 0.5rem;
          align-items: center;
          img {
            width: 2rem;
            height: 2rem;
          }
          span {
            padding: 0.3rem;
            max-width: 70%;
            margin-left: 0.4rem;
            background-color: #fff;
            color: rgb(57, 57, 57);
            font-size: 0.7rem;
            border-radius: 1.4rem;
            border-top-left-radius: 0;
            b {
              margin-top: 0.2rem;
              color: rgb(255, 82, 82);
              font-size: 0.7rem;
              display: block;
              width: 100%;
              text-align: center;
            }
          }
          &.user_say {
            justify-content: flex-end;
            img {
              order: 1;
            }
            span {
              order: 0;
              border-radius: 1.4rem;
              border-top-right-radius: 0;
              margin-right: 0.4rem;
              background-color: #3b95ff;
              color: #fff;
            }
          }
        }
      }
      .layer_bottom {
        background-color: #eff4fa;
        position: fixed;
        width: 100%;
        left: 0;
        bottom: 0;
        .bot_top {
          display: flex;
          justify-content: center;
          height: 1.3rem;
          align-items: center;
          img {
            width: 0.8rem;
            height: 0.8rem;
            margin-right: 0.2rem;
          }
          span {
            font-size: 0.6rem;
            color: rgb(153, 153, 153);
          }
          a {
            color: rgb(81, 163, 255);
            font-size: 0.6rem;
          }
        }
        .bot_input {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: #fff;
          padding: 0 0.6rem;
          height: 2rem;
          span {
            font-size: 0.7rem;
            color: rgb(57, 57, 57);
          }
          input {
              width: 10rem;
              height: 1.5rem;
              background-color: #eaeff5;
              padding: 0 0.5rem;
              border-radius: 0.4rem;
            }
            a {
              width: 3rem;
              height: 1.5rem;
              line-height: 1.5rem;
              text-align: center;
              color: #fff;
              font-size: 0.75rem;
              background-color: #3090ff;
              border-radius: 0.3rem;
            }
        }
      }
    }
  }
}
</style>



